.top-bar {
  padding-block: 24px;
  
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include onTablet {
    padding-block: 36px;
  }

  &__icons {
    display: flex;
    gap: 24px;

    @include onTablet {
      gap: 32px;
    }
  }

  &__logo-link {
    display: flex;
  }

  &__logo {

    height: 38px;

    @include onTablet {
      height: 56px;
    }

    @include hover (transform, scale(1.2));

    animation-duration: $fadeDuration;
    animation-name: fadeInLeft;
  }
}
